import { Component, OnInit } from '@angular/core';
import axios from 'axios';
import { api } from './server';
import { AlertController } from '@ionic/angular';
import { Storage } from '@ionic/storage-angular';

@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html',
  styleUrls: ['app.component.scss'],
})
export class AppComponent implements OnInit {
  public appPages = [
    { title: 'Patients', url: '/patients', icon: 'people' },
    { title: 'Tests', url: '/tests', icon: 'flask' },
    { title: 'Barcode reader', url: '/results', icon: 'barcode' },
    { title: 'Income', url: '/income', icon: 'cash' },
    { title: 'Nations', url: '/nations', icon: 'language' },
    // { title: 'Import', url: '/import', icon: 'cloud-upload' },
    { title: 'Logout', url: '/logout', icon: 'power' },
  ];
  login = {
    username: '',
    password: '',
    loading: false
  }
  user = {
    isLoggedIn: false,
    data: null
  }
  
  SYSTEM = {
    APP_VERSION: '1.0.0',
    APP_NAME: 'SMART LAB'
  }

  constructor(
    public alertController: AlertController,
    private storage: Storage
  ) {}

  async ngOnInit() {
    await this.storage.create();

    // CHECK LOGIN 
    this.storage.get('user').then(data => {
      if (data != null || data != undefined) {
        this.user = data
      }
    })
  }

  signin() {
    this.login.loading = true
    axios.post(api + 'login', {
      userName: this.login.username,
      password: this.login.password
    }).then(res => {
      this.user.isLoggedIn = true;
      this.user.data = res.data;
      this.user.isLoggedIn = true;
      this.storage.set('user',this.user)
    }).catch(async err => {
      let message = await this.alertController.create({
        header: 'Login failed',
        message: 'Invalid username or password.',
        buttons: [
          'OK'
        ]
      });
      await message.present();
    }).finally(() => {
      this.login.loading = false;
    })
  }
}


